import { Component, Inject, OnInit, ViewChild } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import {
  MatDialogRef,
  MAT_DIALOG_DATA,
  MatDialog,
  MatDialogConfig,
} from "@angular/material/dialog";
import { Subject, takeUntil, Subscription } from "rxjs";
import { NotificationServiceService } from "src/@core/Models/Notification/notification-service.service";
import { EmployeesLookupComponent } from "src/app/erp-hr/hr-lookups/configurations-lookups/employees-lookup/employees-lookup.component";
import { FilesService } from "src/app/shared/services/files.service";
import { SnackbarService } from "src/app/shared/services/snackbar.service";
import { AllEmployeesComponent } from "../../../employees/all-employees/all-employees.component";
import { CaseInvestigationMngtService } from "src/app/erp-hr/data/case-services/case-investigations.service";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";

@Component({
  selector: "app-initiate-case-investigation",
  templateUrl: "./initiate-case-investigation.component.html",
  styleUrls: ["./initiate-case-investigation.component.scss"],
})
export class InitiateCaseInvestigationComponent implements OnInit {
  mngForm: FormGroup;

  destroy$: Subject<boolean> = new Subject<boolean>();

  downloadLoading: boolean = false;

  pageFunction: string = "Add";

  hideSubmit: boolean = false;
  isLoading: boolean = true;

  constructor(
    public dialogRef: MatDialogRef<AllEmployeesComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private fb: FormBuilder,
    private snackbar: SnackbarService,
    private caseInvestigationMngtService: CaseInvestigationMngtService,
    private dialog: MatDialog,
    private filesService: FilesService,
    private notificationAPI: NotificationServiceService
  ) { }

  ngOnInit(): void {
    console.log("LOG DATA = ", this.data);
    this.pageFunction = this.data.action;
    this.formData = this.data.record;
    this.generateForm(this.formData);

    this.getPage();
  }
  ngAfterViewInit() {
    if (this.data.action !== "Add") {
      this.generateForm(this.formData);
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }

  disableTransactionType: boolean = true;
  getPage() {
    this.disableTransactionType = true;
    console.log("this.data.action :", this.data.action);
    if (this.data.action === "Add") {
      this.isLoading = false;
      this.generateForm(this.data.record);
    } else if (this.data.action === "Update") {
      this.generateForm(this.data.record);
    } else if (this.data.action === "View") {
      this.generateForm(this.data.record);
      this.activateViewMode();
    }
    this.isLoading = false;
  }

  formData: any;
  generateForm(formData?): void {
    const fromDate = formData?.fromDate ?? new Date();
    const toDate = new Date(fromDate);
    toDate.setDate(toDate.getDate() + 7);
    console.log("generateForm formData :: ", formData);
    this.mngForm = this.fb.group({
      id: [formData?.id ?? ""],
      caseNo: [formData?.caseNo ?? ""],
      comments: [formData?.comments ?? ""],
      fromDate: [formData?.comments ?? fromDate, [Validators.required]],
      toDate: [formData?.comments ?? toDate, [Validators.required]],
      attachment: [formData?.attachment ?? "", [Validators.required]],
      investigators: [formData?.investigators ?? [], [Validators.required]],
    });
  }

  viewMode: boolean = false;
  activateViewMode() {
    this.viewMode = true;
  }

  //********************************************************************************************************************** */
  //********************************************************************************************************************** */
  file_name: any;
  base64File: string;
  loading: boolean = false;
  handleFileInput(event: any) {
    const file: File = event.target.files[0];
    const reader = new FileReader();

    reader.onloadend = () => {
      this.base64File = reader.result as string;
      this.file_name = file.name;
      this.mngForm.patchValue({
        attachment: this.base64File,
      });
    };

    if (file) {
      reader.readAsDataURL(file);
    }
  }

  getDocument() {
    this.loading = true;
    let params = {
      id: this.data.formData.id,
    };
    this.caseInvestigationMngtService.findById(params).subscribe(
      (res) => {
        console.log(res);
        if (res.entity.attachment) {
          this.loading = false;
          this.downloadDocument(res.entity.attachment);
        } else {
          this.loading = false;
          this.notificationAPI.alertWarning("No Document Attached!");
        }
      },
      (err) => {
        this.loading = false;
      }
    );
  }

  downloadDocument(fileToDownload: string) {
    let fileName = "LeaveForm";

    fetch(fileToDownload)
      .then((response) => response.blob())
      .then((blob) => {
        const url = window.URL.createObjectURL(blob);
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", `${fileName}`);
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
      });
  }

  //********************************************************************************************************************** */

  //********************************************************************************************************************** */
  dataSource = new MatTableDataSource<any>([]);
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  displayedColumns: string[] = ["id", "empPf", "empName", "action"];
  tranForm: FormGroup;
  showParTranForm = false;
  parTranAction: string = "";
  selectedParTranIndex: number;
  debitTotal: number = 0;
  creditTotal: number = 0;
  balance: number = 0;

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }
  // generateSubForm(formData?): void {
  //   this.tranForm = this.fb.group({
  //     id: [formData?.id ?? ""],
  //     description: [formData?.narration ?? "", [Validators.required]],
  //   });
  // }
  resetFormAndHide() {
    this.mngForm.patchValue({
      investigators: this.dataSource.data,
    });
    this.tranForm.reset();
    this.showParTranForm = false;
  }

  addParTran() {
    // this.parTranAction = "Add";
    // this.showParTranForm = true;
    this.employeeLookup();
  }

  refresh() {
    this.dataSource.data = [...this.dataSource.data];
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  editParTran(data: any, index: number) {
    this.parTranAction = "Update";
    this.selectedParTranIndex = index;
    //this.generateSubForm(data);
    this.showParTranForm = true;
  }

  pushToDataSource() {
    if (this.parTranAction === "Add") {
      this.dataSource.data.push(this.tranForm.value);
    } else if (this.parTranAction === "Update") {
      let indexToUpdate: number;

      if (this.tranForm.value.id) {
        indexToUpdate = this.dataSource.data.findIndex(
          (item) => item.id === this.tranForm.value.id
        );
      } else {
        indexToUpdate = this.selectedParTranIndex;
      }

      if (indexToUpdate !== -1) {
        this.dataSource.data[indexToUpdate] = this.tranForm.value;
      }
    }

    this.resetFormAndHide();
    this.refresh();
  }

  cancelParTran() {
    this.resetFormAndHide();
  }

  deleteParTran(index: number) {
    this.dataSource.data.splice(index, 1);
    this.refresh();
  }

  // **************************************************************************************************

  posting: boolean = false;

  response: any;
  submit() {
    console.log("this.mngForm.value: ", this.mngForm.value);
    this.posting = true;
    if (this.pageFunction === "Add") {
      this.caseInvestigationMngtService
        .create(this.mngForm.value)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            this.response = res;
            console.log("res: ", res);
            if (res.statusCode == 201) {
              this.snackbar.showNotification("snackbar-success", res.message);
            } else {
              this.snackbar.showNotification("snackbar-danger", res.message);
            }
          },
          error: (err) => {
            this.snackbar.showNotification("snackbar-danger", err.message);
          },
          complete: () => {
            this.posting = false;
            setTimeout(() => {
              this.onNoClick();
            }, 3000);
          },
        }),
        Subscription;
    } else if (this.pageFunction === "Update") {
      this.caseInvestigationMngtService
        .update(this.mngForm.value)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            this.response = res;
            console.log("resres:: ", res);
            if (res.statusCode == 200) {
              this.snackbar.showNotification("snackbar-success", res.message);
            } else {
              this.snackbar.showNotification("snackbar-danger", res.message);
            }
          },
          error: (err) => {
            this.snackbar.showNotification("snackbar-danger", err.message);
          },
          complete: () => {
            this.posting = false;
            setTimeout(() => {
              this.onNoClick();
            }, 3000);
          },
        }),
        Subscription;
    }
  }

  onNoClick(): void {
    this.dialogRef.close({ event: "close", data: this.response });
  }

  selectedItem: any;
  employeeLookup() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.disableClose = true;
    dialogConfig.width = "800px";
    dialogConfig.data = {
      action: "MultipleEmployee",
      selected: this.selectedItem,
    };
    const dialogRef = this.dialog.open(EmployeesLookupComponent, dialogConfig);
    dialogRef.afterClosed().subscribe((res) => {
      if (res && res.data && res.data.length > 0) {
        res.data.forEach((employeeInfo) => {
          const { empPf, empName } = employeeInfo;

          if (this.dataSource.data) {
            const existingItemIndex = this.dataSource.data.findIndex(
              (item) => item.empPf === empPf || item.empName === empName
            );

            if (existingItemIndex !== -1) {
              this.dataSource.data[existingItemIndex] = {
                ...this.dataSource.data[existingItemIndex],
                empPf,
                empName,
              };
            } else {
              this.dataSource.data.push({
                empPf,
                empName,
              });
            }
          }
        });

        this.refresh();
      }
    });
  }
}
